.empty-todos{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    color: rgb(0, 255, 51);
    font-size: 20px;
}

@keyframes pulso {
    0% {
        transform: scale(1);
        opacity: 1;
    }
    50% {
        transform: scale(1.1);
        opacity: 0.7;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}

.empty-todos{
    animation: pulso 2s infinite;
}