.loading-todo{
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(255, 149, 0);

}

.loading-todo span{
    font-size: 100px;
    animation: blink 1.4s infinite both
};

.loading-todo span:nth-child(2){
    animation-delay: 0.2s;
}

.loading-todo span:nth-child(3){
    animation-delay: 0.4s;
}

@keyframes blink{
    0%{
        opacity: 0.2;
    }
    20%{
        opacity: 1;
    }
    100%{
        opacity: 0.2;
    }
}

