form {
    display: flex; /* Usar flexbox para el formulario */
    flex-direction: column; /* Alinear los elementos en columna */
    height: 100%; /* Asegúrate de que el formulario ocupe el espacio disponible */
    padding: 20px; /* Espaciado interno */
    box-sizing: border-box; /* Incluye el padding en el ancho total */
}

label {
    margin-bottom: 10px; /* Espacio entre la etiqueta y el textarea */
}

textarea {
    resize: none; /* Evitar que el textarea se redimensione */
    height: 100px; /* Altura fija para el textarea */
    margin-bottom: 20px; /* Espacio entre el textarea y los botones */
}

.TodoForm-buttonContainer {
    display: flex; /* Usar flexbox para los botones */
    justify-content: space-between; /* Espacio entre los botones */
    margin-top: auto; /* Empujar los botones hacia la parte inferior del formulario */
}

.TodoForm-button {
    padding: 10px 15px; /* Espaciado interno para los botones */
    border: none; /* Sin borde */
    border-radius: 5px; /* Bordes redondeados */
    cursor: pointer; /* Cambiar el cursor al pasar el mouse */
}

.TodoForm-button--cancel {
    background-color: #f44336; /* Color de fondo para el botón de cancelar */
    color: white; /* Color del texto */
}

.TodoForm-button--add {
    background-color: #4CAF50; /* Color de fondo para el botón de añadir */
    color: white; /* Color del texto */
}

.TodoForm-button--cancel:hover {
    background-color: #d32f2f; /* Color de fondo al pasar el mouse */
}

.TodoForm-button--add:hover {
    background-color: #45a049; /* Color de fondo al pasar el mouse */
}